import { Component, OnInit, Input } from '@angular/core';
import { ConnectService } from '../core/connect.service';

@Component({
  selector: 'app-artist',
  templateUrl: './artist.component.html',
  styleUrls: ['./artist.component.scss']
})
export class ArtistComponent implements OnInit {

  constructor(public connectService: ConnectService) { }

  @Input() public artist;
  public baseURL = "http://sonoorarecords.com.br/api/";
  public perfilUrl;
  public fotos:any = [];

  ngOnInit() {
    this.getData();
  }

  async getData(){
    await this.connectService.getPerfilPicture(this.artist.codigo).subscribe(res => {
      this.perfilUrl = this.baseURL+res[0].name_picture
    });

    await this.connectService.getArtistPicture(this.artist.codigo).subscribe(res => {
      res = res.map(item => {
        item.name_picture = this.baseURL+item.name_picture;
        this.fotos.push(item)
      })
    });
  }

}
